
body {
    position: relative;
}

.button-up {
    position: fixed;
    bottom: 40px;
    right: 40px;
    background: #000000c9;
    color: #fff;
    width: 60px;
    height: 60px;
    text-align: center;
    padding: 16px;
    border-radius: 15px;
    display: none;
    z-index: 15;
    align-items: center;
    border-radius: 30px;
    &:hover {
        color: #fff;
    }
}
 .active {
    display: fixed;
}


// -----------------------------------------------.delivery-iption--------------------------------

.intro {
    padding-top: 300px;
}

.delivery-option {
    margin-top: 100px;
    padding-bottom: 60px;
    .title {
        margin-bottom: 50px;
        @include mq('tablet-mid', max) {
            justify-content: center;
        }
    }
    .block {
        margin-top: 50px;
        @include mq('phablet', max) {
            display: block!important;
        } 
    }
    .empty {
        display: inline-block;
        color: transparent;
        -webkit-text-stroke: 1px #000000;
        margin-left: 1rem;
    }
    .icon {
        @include mq('tablet', max) {
            display: flex;
            justify-content: center;
            flex-direction: column;
        }
    }
    img {
            object-fit: contain;
            max-width: auto!important;
    }
    h2 {
        @include mq('phablet', max) {
            font-size: 2.4rem;
        }
        @include mq('phone-wide', max) {
            font-size: 1.7  rem;
        }
    }
    h4 {
        font-weight: 600;
    }
    p {
        text-align: left;
        font-size: 1.3rem;
    }
}

//  ----------------------------------------------------.formats------------------------------------------

.formats {
    background-color: #f6f9ff;
    padding-top: 50px;
    .col-md-4 {
        padding: 15px;
    }

    .card {
        width: 100%;
        height: 300px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        flex-wrap: wrap;
        align-items: center;
        background-color: #ffffff;
        transition: all .3s ease-out .1s;
        &:hover
        {
            color: #7d7fa9;
            box-shadow: 0px 10px 22.75px 12.25px rgba(0, 0, 0, 0.07);

        }
           
           
          
        
        @include mq('desktop-small', max) {
            height: 300px;
        }
        
        @include mq('tablet-mid', max) {
            height: 200px;
        }
        @include mq('tablet', max) {
            height: auto;
            display: flex;
            flex-direction: row;
            justify-content: start;
        }
        @include mq('phone', max) {
            flex-direction: column;
        }
        
    }

    h2 {
        margin-bottom: 50px;
    }

    h5 {
        margin-bottom: 0;
        font-size: 22px;
        font-weight: 600;
        text-align: center;
        padding: 0 5px;
            @include mq('phone-wide', max){
                font-size: 1rem;
            }
            @include mq('tablet', max)
            {
                text-align: center;
                font-size: 1.4rem;
            }
            @include mq('phablet', max) {
                font-size: 1.2rem;        
            }
            @include mq('phone-wide', max) {
                font-size: 1rem;        
            }
        }
    img {
        width: 120px;
            @include mq('tablet-mid', max){
            width: 100px;
            margin: 0 45px;
            }
            @include mq('phablet', max) {
                width: 55px;
            }   
        }
}


// -------------------------------------------courses-----------------------------------
  
.courses {
    .accordion-collapse {
        margin-bottom: 90px;
    }


    .card {
        border: 0;
        position: relative;
        margin-top: 15px;
    }
    
    .header_title {
        margin: 0;
        padding: 20px 20px;
        font-size: 1.5rem;
        color: rgb(0, 0, 0);
        font-weight: 600;
        box-shadow: 0px 8px 18.75px 6.25px rgba(0, 0, 0, 0.03);
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        border-left: 8px solid #4786ff;
            @include mq('phablet', max) {
                font-size: 1.2rem;
            }
    }
    .card-header {
            padding: 0;
    }

    .card-header {
        background-color: #fff;
        border: 0;
    }

    .text-left:hover {
    cursor: pointer;
    }

    .accordions .text-left::after {
    content: "-";
        position: absolute;
        top: 3px;
        right: 15px;
        font-size: 2.6rem;
        font-weight: 700;
        color: rgb(226, 226, 226);
    }

    .accordions .text-left.collapsed::after {
    content: '+';
    }
    h2 {
        &.title{
        margin-top: 50px;
        margin-bottom: 45px;
        text-align: center;
        }
    }
    p {
        .text {
        font-size: 0.75rem;
        color: #7d7d7d;
        font-weight: 400;
        }
    }
      
    .card-body
    {
        position: relative;
        border: 1px solid #d7d7d7;
        border-top: 0;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
    }
}
  



// --------------------------------------------------------.methotical-----------------------------------------

.methodical {
    .row {
        margin: 70px 0;
    }
    .card {
        flex-direction: row;
        box-shadow: 0px 4px 22.75px 12.25px rgba(0, 0, 0, 0.03);
        border-radius: 8px;
        margin: 15px 0px;
    }   
    img {
        object-fit: contain;
        padding: 25px 30px;  
    }
    h5 {
       margin: 0;
       padding: 40px 20px 20px 15px;
       font-size: 1.5rem;
       font-weight: 600;
            @include mq('phablet', max) {
            padding: 40px 20px;
            font-size: 1rem;
            }
    }

    .card {
        position: relative;
        overflow: hidden;
        
        &:hover .hidden-text {
            top: 0;
        }
    }
    .hidden-text {
        position: absolute;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 20px;
        overflow-y: auto;
        background: #fff;
        z-index: 1;
        transition: .5s ease;
    }
    .btn {
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: 80px;
        
    }
    p {
        font-size: 0.9rem;
    }

}

section.about
{
    background-color: #f6f9ff;
    .row
    {
        margin: 0;
    }

    .col-lg-6
    {
        padding: 40px;
        border: 1px solid #d1d4d9;
        border-radius: 6px;
        background: #fff;
        transition: .2s ease;

        &:hover
        {
            transform: scale(1.1);
            z-index: 1;
            box-shadow: 0 0 1rem #d7d7d7;
        }
    }

    .title
    {
        display: flex;
        align-items: center;
    }
    h2 {
        text-align: center;
    }
    h5
    {
        font-size: 1.4rem;
        font-weight: 600;
        margin: 0;
        color: #7d7fa9;
    }

    p
    {
        font-size: 1.1rem;
        font-weight: 400;
        color: #7d7fa9;
    }

    @include mq('phablet', max)
    {
        .col-lg-6
        {
            padding: 20px;
        }

        img
        {
            width: 100px;
        }

        h5
        {
